.blind {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  margin: -1px;
  overflow: hidden; }

.momentum {
  background-color: #f9f9f9;
  margin: auto;
  padding-top: 50px; }

.momentum-title {
  text-align: center;
  font-size: 42px;
  line-height: 54px;
  font-weight: bold; }

.momentum-today {
  width: 500px;
  margin: 20px auto 0;
  padding: 20px 0;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2); }

.momentum-date {
  width: 300px;
  margin: auto;
  text-align: center; }

.momentum-weather {
  width: 300px;
  margin: 10px auto 0;
  text-align: center; }
  .momentum-weather .weather-wrap::after {
    content: '';
    display: block;
    clear: both; }
  .momentum-weather .weather-img {
    float: left; }
  .momentum-weather .temperature-wrap {
    position: relative; }
  .momentum-weather .current-temper {
    font-size: 52px;
    line-height: 100px;
    font-weight: bold; }
  .momentum-weather .sub-temper {
    position: absolute;
    top: 0;
    right: 0; }

.momentum-clock {
  text-align: center;
  margin-top: 20px; }
  .momentum-clock .clock-time {
    font-size: 72px;
    line-height: 72px;
    font-weight: bold; }

.momentum-todos {
  width: 500px;
  margin: 30px auto 0;
  position: relative; }
  .momentum-todos .todo-input {
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    line-height: 40px;
    border-radius: 5px;
    border: 1px solid #000;
    padding: 3px 50px 3px 10px;
    font-size: 14px; }
  .momentum-todos .todo-submit-btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 40px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: orange;
    text-align: center;
    border: 1px solid brown; }
  .momentum-todos .todo-list {
    margin-top: 20px; }
  .momentum-todos .todo-item {
    padding: 10px;
    border: 1px solid #000;
    border-radius: 5px;
    height: 18px;
    line-height: 18px;
    font-size: 14px; }
    .momentum-todos .todo-item + .todo-item {
      margin-top: 10px; }

.greeting-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5); }

.greeting-body {
  position: absolute;
  width: 450px;
  height: 250px;
  top: 50%;
  left: 50%;
  margin-top: -150px;
  margin-left: -250px;
  background-color: #fff;
  border-radius: 5px;
  padding: 25px; }
